'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Image from 'next/image';
import EduPalLogo from '@/assets/edupallogo.jpg';

interface OnboardingSlide {
    id: number;
    title: string;
    description: string;
    icon: string;
}

export default function Onboarding() {
    const router = useRouter();
    const [currentSlide, setCurrentSlide] = useState(0);

    const slides: OnboardingSlide[] = [
        {
            id: 1,
            title: "Find Past Questions",
            description: "Access a massive repository of past exam papers organized by institution and department.",
            icon: "search_insights"
        },
        {
            id: 2,
            title: "Join Virtual Classes",
            description: "Engage in real-time learning sessions with your lecturers and peers from anywhere.",
            icon: "video_call"
        },
        {
            id: 3,
            title: "Connect & Collaborate",
            description: "Build your digital campus community. Join study groups and share resources.",
            icon: "groups"
        }
    ];

    const nextSlide = () => {
        if (currentSlide < slides.length - 1) {
            setCurrentSlide(currentSlide + 1);
        } else {
            router.push('/login');
        }
    };

    const skipOnboarding = () => {
        router.push('/login');
    };

    return (
        <div className="relative flex h-screen w-full flex-col bg-background-light dark:bg-background-dark overflow-hidden">
            {/* Top App Bar / Header */}
            <div className="flex items-center justify-between p-6">
                <div className="flex items-center gap-2">
                    <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center overflow-hidden">
                        <Image
                            src={EduPalLogo}
                            alt="EduPal"
                            width={32}
                            height={32}
                            className="object-cover"
                        />
                    </div>
                    <span className="text-xl font-bold tracking-tight text-slate-900 dark:text-white">EduPal</span>
                </div>
                {currentSlide < slides.length - 1 && (
                    <button
                        onClick={skipOnboarding}
                        className="text-slate-500 dark:text-primary/80 text-sm font-semibold hover:text-primary transition-colors"
                    >
                        Skip
                    </button>
                )}
            </div>

            {/* Main Visual Content */}
            <div className="flex-1 flex flex-col items-center justify-center px-6">
                <div className="w-full max-w-md">
                    {/* Visual Container */}
                    <div className="aspect-square w-full rounded-3xl bg-primary/10 flex items-center justify-center relative overflow-hidden">
                        {/* Decorative Background elements */}
                        <div className="absolute -top-10 -right-10 w-40 h-40 bg-primary/20 rounded-full blur-3xl"></div>
                        <div className="absolute -bottom-10 -left-10 w-40 h-40 bg-primary/10 rounded-full blur-3xl"></div>

                        {/* Main Illustration */}
                        <div className="relative z-10 flex flex-col items-center">
                            <div className="w-32 h-32 bg-primary rounded-2xl flex items-center justify-center shadow-xl shadow-primary/20">
                                <span className="material-symbols-outlined text-background-dark text-6xl">
                                    {slides[currentSlide].icon}
                                </span>
                            </div>
                            <div className="mt-6 flex gap-3">
                                <div className="w-12 h-1 bg-primary/30 rounded-full"></div>
                                <div className="w-16 h-1 bg-primary/50 rounded-full"></div>
                                <div className="w-8 h-1 bg-primary/20 rounded-full"></div>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Text Content */}
                <div className="mt-12 text-center max-w-sm">
                    <h1 className="text-3xl font-bold leading-tight mb-4 text-slate-900 dark:text-white">
                        {slides[currentSlide].title}
                    </h1>
                    <p className="text-slate-600 dark:text-slate-400 text-base leading-relaxed">
                        {slides[currentSlide].description}
                    </p>
                </div>
            </div>

            {/* Footer / Navigation */}
            <div className="p-8 flex flex-col items-center gap-8">
                {/* Progress Indicators */}
                <div className="flex flex-row items-center justify-center gap-3">
                    {slides.map((_, index) => (
                        <div
                            key={index}
                            className={`h-2 rounded-full transition-all duration-300 ${index === currentSlide
                                    ? 'w-8 bg-primary'
                                    : 'w-2 bg-slate-300 dark:bg-slate-700'
                                }`}
                        />
                    ))}
                </div>

                {/* Action Button */}
                <button
                    onClick={nextSlide}
                    className="w-full max-w-xs bg-primary text-background-dark font-bold py-4 rounded-xl flex items-center justify-center gap-2 hover:opacity-90 transition-opacity active:scale-95"
                >
                    <span>{currentSlide === slides.length - 1 ? 'Get Started' : 'Next'}</span>
                    <span className="material-symbols-outlined">arrow_forward</span>
                </button>
            </div>
        </div>
    );
}
